"use client";
import { useEffect, useState } from "react";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { useLayout } from "@/hooks/useLayout";
import { useTransactionStore } from "@/utils/store";
import { TransactionStore } from "@/utils/store";
import MobileTransactionCard from "./mobile-transaction";

export default function TransactionTable() {
  const { isMobile } = useLayout();
  const {
    transactions,
    setFetchingTransactions,
    setTransactions,
    isFetchingTransactions,
  } = useTransactionStore();

  // Mobile view - cards
  if (isMobile) {
    return (
      <div className="space-y-3 px-1">
        <div className="flex justify-between items-center mb-4">
          <h3 className="text-lg font-semibold">Transactions</h3>
        </div>

        {transactions.map((transaction) => (
          <Mo key={transaction.id} transaction={transaction} />
        ))}
      </div>
    );
  }

  // Desktop view - table
  return (
    <Table className="w-full">
      <TableHeader>
        <TableRow>
          <TableHead className="w-[100px]">Invoice</TableHead>
          <TableHead>Status</TableHead>
          <TableHead>Method</TableHead>
          <TableHead className="text-right">Amount</TableHead>
        </TableRow>
      </TableHeader>
      <TableBody>
        {invoices.map((invoice) => (
          <TableRow key={invoice.invoice}>
            <TableCell className="font-medium">{invoice.invoice}</TableCell>
            <TableCell>{invoice.paymentStatus}</TableCell>
            <TableCell>{invoice.paymentMethod}</TableCell>
            <TableCell className="text-right">{invoice.totalAmount}</TableCell>
          </TableRow>
        ))}
      </TableBody>
    </Table>
  );
}
